import {
  Button,
  Card, CardContent, TextField, Typography,
} from '@mui/material';
import CreateIcon from '@mui/icons-material/Create';
import Image from 'mui-image';
import { useState } from 'react';
import candle_header from '../yakir title.jpeg';

const margin = 1;

export default function DinamicImage() {
  const [personalText, setPersonalText] = useState();
  const [readyToWrite, setReadyToWrite] = useState(false);

  function changeInput(e) {
    setPersonalText(e.target.value);
  }

  return (

    <>
      {
        !readyToWrite ?
          <Button
            sx={{ my: margin }}
            fullWidth
            variant="outlined"
            size="large" onClick={() => { setReadyToWrite(true) }}
            endIcon={<CreateIcon />}>
            ניתן להוסיף שם אישי
          </Button>
          :
          <TextField
            onChange={changeInput}
            inputProps={{ maxLength: 40 }}
            label="...זוכרים באהבה את"
            sx={{ my: margin, mx: 10 }}
            fullWidth
            autoFocus            
          />
      }
      <div id="print">
        <Card
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            bgcolor: '#f4edd5',
          }}
        >
          <Image
            src={candle_header}
          />
          <CardContent>
            <Typography
              style={{
                fontFamily: 'Assistant',
                color: '#1da398',
              }}
              gutterBottom
              variant="h6"
              component="div"
            >
              {personalText ? 'זוכרים באהבה את' : 'מחבקים באהבה את'}
            </Typography>
            <Typography
              style={{
                fontFamily: 'Assistant',
                color: '#1da398',
              }}
              gutterBottom
              variant="h4"
              component="div"
            >
              {personalText ? ` ${personalText}` : 'משפחות השכול האזרחי'}
            </Typography>
          </CardContent>
        </Card>
      </div>
    </>
  );
}
